import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import file from './content.js';

class Nav extends React.Component {
  render() {
    let navigation = []
    file.NAV.forEach( item => {
      let container = 
      <div className="left">
        <a href={item.href} className="active">{item.title}</a>
      </div>
    navigation.push(container);
    })

    return(
      <nav>
        {navigation}
      </nav>
    )
  }
}

class Hero extends React.Component {
  render() {
    return (
      <div id="hero" className="frame">
        <div className="checklist">
            <h2>Checklist</h2>
            <div className="check">
                <div className="checkbox"></div><span>Use a safe browser</span>
            </div>
            <div className="check">
                <div className="checkbox"></div><span>Make your browser more safe</span>
            </div>
            <div className="check">
                <div className="checkbox"></div><span>Search online without worry</span>
            </div>
            <span>More coming soon</span>
        </div>
        <div className="value-prop">
            <h1>The Practical Privacy Guide</h1>
            <p>The guide for you who just wants it to work.</p>
            <a className="primary" href="#browser">Start the guide</a>
        </div>
    </div>
    )
  }
}

class Sections extends React.Component {
  render() {
    let sections = [];
    file.CONTENT.forEach( section => {
      let container = 
      <div id={section.id} className="frame">
        <h1>{section.title}</h1>
        <QA info={section.qa}/>
        <DownloadSection programs={section.programs}/>
        <Alert name={section.type}/>
        <Continue id={section.id}/>
      </div>
     sections.push(container);
    } )
    return (
      <>
      {sections}
      </>
    )
  }
}

class Done extends React.Component {
  render() {
    return (
      <div id="done" class="frame">
      <h1>Congratulations</h1>
     <img src={require("./images/confetti.png").default} alt="A confetti canon" />
      <div class="section">
      <h2>You have taken a step towards a better future.</h2>
      <p>If you want to learn more about the web and some thoughts on digitalization, how it effects us as individuals and as a society, check out....</p>
  </div>
  <div class="continue">
      <a class="secondary large" href="#">Done</a>
      <img class="arrow" src={require("./images/Arrow-down.svg").default} alt="arrow pointing down" />
  </div>
  </div>
    )
  }
}

class Alert extends Sections {
  render() {
  return(
    <span class="alert">Come back to this page when you have installed the {this.props.name}</span>
  )
  }
}

class Continue extends Sections {
  render() {
    let href;
    for( let i = 0; i < file.CONTENT.length; i++ ) {
      let con = file.CONTENT[i];
      if (con.id === this.props.id) {
        file.CONTENT[i+1]? href = "#" + file.CONTENT[i+1].id : href = "#done";
        break;
      }
    }
    return (
      <div class="continue">
        <a class="secondary large" href={href}>Continue</a>
        <img class="arrow" src={require("./images/Arrow-down.svg").default} alt="arrow pointing down" />
      </div>
    )
  }
}

class QA extends Sections {
  render() {
    let qa = [];
    this.props.info.forEach( prop => {
               let container =
               <div className="qa">
                 <h2>{prop.q}</h2>
                 <p>{prop.a}</p>
               </div>;
               qa.push(container);
    } )
    return(
      <>{qa}</>
    )
  }
}

class DownloadSection extends Sections {
  render() {
    let download = [];
    this.props.programs.forEach( program => {

      let container =
      <div className="download-section">
      <ImageBox 
      image={program.image} 
      src={program.image.src} 
      alt={program.image.alt} 
      title={program.name}/>
      <Download 
      title={program.name}
      link={program.downloadsrc}
      home={program.homepage}
      />
      </div>
      ;
      download.push(container);
    } )
  return(
    <>
      {download}
    </>
  )
  }
}

class ImageBox extends DownloadSection {
  render() {
    let images = [];
    const logo = require("./images/"+ this.props.src.toString()).default;
      let section = 
      <div className="image-box">
        <img src={logo} alt={this.props.alt} />
        <span className="image-title">{this.props.title}</span>
      </div>;
      images.push(section);
  return(
    <>
    {images}
    </>
  )
  }
}

class Download extends DownloadSection {
  render() {
    let sections = [];
      let section = 
      <div className="download">
        <a className="primary" href={this.props.link} target="_blank" rel="noreferrer">Install {this.props.name}</a>
        <span className="button-undertext">You will be forwarded to {this.props.home}</span>
      </div>
      sections.push(section);

  return(
    <>
    {sections}
    </>
  )
  }
}
  
  // ========================================
  
  class App extends React.Component {
    render() {
      return (
        <>
        <Nav />
        <main>
        <Hero />
        <Sections/>
        <Done />
        </main>
        </>
      )
    }
  }

  ReactDOM.render(
    <App />,
    document.getElementById('root')
  );
  